import React from 'react';
import Table from './Table';
import type { InventoryRecord } from '../services/admin';

const columns = [
    { accessorKey: 'description', header: 'Ni dung', enableResizing: true },
    { accessorKey: 'createdAt', header: 'Ngy to', enableResizing: true },
    { accessorKey: 'createdBy', header: 'Ngi to', enableResizing: true },
];

const InventoryPage: React.FC<{ data: InventoryRecord[] }> = ({ data }) => (
    <Table<InventoryRecord> data={data} columns={columns} />
);

export default InventoryPage;